/* =========================
   Account page — clean CSS
   ========================= */

/* ---------- Layout général ---------- */
.account-page{
    max-width:1200px;
    margin:0 auto;
    padding:24px 16px;
    display:grid;
    grid-template-columns:280px 1fr;       /* sidebar | contenu */
    gap:18px;
  }
  
  /* ---------- Sidebar ---------- */
  .account-sidebar{
    background:#fff;
    border:1px solid #e5e7eb;
    border-radius:12px;
    box-shadow:0 8px 24px rgba(0,0,0,.06);
    padding:14px;
    position:sticky;
    top:100px;
    align-self:start;
  
    display:flex;
    flex-direction:column;
    align-items:stretch;
  }
  
  /* Identité */
  .account-user{
    display:grid;
    grid-template-columns:56px 1fr;
    gap:10px;
    align-items:center;
    margin-bottom:12px;
  }
  .account-avatar{
    width:56px;height:56px;border-radius:50%;
    object-fit:cover;background:#f3f4f6;display:block;
  }
  .account-avatar--emoji{
    width:56px;height:56px;border-radius:50%;
    display:grid;place-items:center;font-size:28px;background:#f3f4f6;
  }
  .account-identity{ min-width:0; }
  .account-name{
    font-weight:900;color:#111827;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;
  }
  .account-ref{ color:#6b7280;font-size:.9rem; }
  
  /* Bouton déconnexion – pleine largeur, fond noir */
  .account-sidebar .account-user .account-logout{
    grid-column: 1 / -1;         /* ← occupe les 2 colonnes de la grille */
    width: 100%;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
  
    min-height: 44px;
    padding: 12px 14px;
    margin-top: 10px;
  
    background: #000;            /* fond noir */
    color: #fff;
    border: 1px solid rgba(255,255,255,.08);
    border-radius: 12px;
    font-weight: 800;
  }
  .account-sidebar .account-user .account-logout i{
    font-size: 18px;
    line-height: 1;
  }
  .account-logout:hover{ filter:brightness(1.05); }
  .account-logout:active{ transform:translateY(1px); }
  
  /* Menu */
  .account-nav{
    display:grid; gap:8px; margin-top:12px;
  }
  .account-nav__item{
    text-align:left;
    border:1px solid #e5e7eb;border-radius:10px;background:#fff;
    padding:10px 12px; cursor:pointer; font-weight:800; color:#111827;
  }
  .account-nav__item:hover{ background:#f9fafb; }
  .account-nav__item.is-active{
    background:linear-gradient(135deg,#3b79ff,#2c5dff);
    color:#fff;border-color:transparent;
  }
  
  /* ---------- Zone principale ---------- */
  .account-main{ display:grid; gap:16px; }
  
  /* Bandeau haut */
  .account-hero{
    background:#fff;border:1px solid #e5e7eb;border-radius:12px;
    padding:16px;display:flex;align-items:center;justify-content:space-between;
    gap:12px;
  }
  .account-counter{ display:flex;align-items:center;gap:10px; }
  .account-counter__num{
    width:46px;height:46px;border-radius:12px;background:#2a60f5;color:#fff;
    display:grid;place-items:center;font-weight:900;font-size:1.1rem;
  }
  .account-counter__label{ font-weight:800;color:#374151; }
  .account-hero__img{ height:56px; }
  
  /* ---------- Commandes ---------- */
  .orders-header{
    display:flex;align-items:center;justify-content:space-between;gap:10px;
  }
  .orders-title{ margin:0;font-weight:900; }
  .account-select{ max-width:260px; font-weight:700; }
  
  .orders-table{
    background:#fff;border:1px solid #e5e7eb;border-radius:12px;overflow:hidden;
    box-shadow:0 8px 24px rgba(0,0,0,.06);
  }
  .orders-head{
    display:grid;grid-template-columns:1.2fr .8fr .8fr .8fr 120px;
    gap:10px;padding:12px 14px;border-bottom:1px solid #f0f0f0;
    font-weight:900;color:#374151;background:#fafafa;
  }
  .col-actions{ text-align:right; }
  
  .order-row{ border-bottom:1px solid #f5f5f5; }
  .order-grid{
    display:grid;grid-template-columns:1.2fr .8fr .8fr .8fr 120px;
    gap:10px;align-items:center;padding:12px 14px;
  }
  .order-id{ font-weight:800; }
  .order-price{ font-weight:900; }
  .order-status{ font-weight:800;color:#2563eb; }
  
  .order-detail-btn{
    justify-self:end;border:1px solid #e5e7eb;border-radius:10px;background:#fff;
    padding:8px 10px;cursor:pointer;font-weight:800;
  }
  .order-detail-btn:hover{ background:#f9fafb; }
  
  /* Détails d’une commande */
  .order-details{
    background:#ffffff;border-top:1px dashed #eee;padding:12px 14px;display:grid;gap:8px;
  }
  .order-item{
    display:grid;grid-template-columns:1fr .5fr auto;gap:10px;align-items:center;
  }
  .order-item--shipping .shipping-label{ font-weight:700; }
  
  .order-actions{ display:flex;gap:10px;margin-top:6px; }
  
  /* Vide */
  .orders-empty{ padding:16px;color:#6b7280; }
  
  /* ---------- Boutons génériques ---------- */
  .gbtn{
    display:inline-flex;
    align-items:center;
    justify-content:center;
    gap:.4rem;
    padding:10px 14px;
    border-radius:12px;
    font-weight:900;
    border:0;
    cursor:pointer;
    transition:filter .15s ease;
  }
  .gbtn--primary{
    background:linear-gradient(135deg,#3b79ff,#2c5dff);
    color:#fff;
  }
  .gbtn--primary:hover{ filter:brightness(.97); }
  .gbtn--primary:active{ filter:brightness(.93); }
  
  /* ---------- Liens (dont tracking) ---------- */
  .order-item a{
    color:#2563eb;
    text-decoration:underline;
    font-weight:800;
  }
  .order-item a:hover{ text-decoration:none; }
  
  /* ---------- Placeholder générique ---------- */
  .account-placeholder{
    background:#fff;border:1px solid #e5e7eb;border-radius:12px;
    padding:18px;color:#6b7280;
  }
  
  /* ---------- Responsive ---------- */
  @media (max-width:1000px){
    .orders-head,
    .order-grid{
      grid-template-columns:1.2fr .8fr .8fr 100px; /* fusion statut/prix bouton */
    }
    .order-status{ display:none; }
  }
  
  @media (max-width:900px){
    .account-page{ grid-template-columns:1fr; }
    .account-sidebar{ position:static; top:auto; }
    .orders-header{ flex-direction:column; align-items:flex-start; gap:8px; }
    .account-select{ width:100%; max-width:none; }
  }
  
  @media (max-width:560px){
    .orders-head{ display:none; }
    .order-grid{
      grid-template-columns:1fr auto; gap:8px;
    }
    .order-grid .order-price{ justify-self:end; }
    .order-grid span:nth-child(3){ /* date */
      grid-column:1 / -1; color:#6b7280;
    }
    .order-detail-btn{ grid-column:1 / -1; justify-self:stretch; }
    .order-item{ grid-template-columns:1fr auto auto; }
  }
  


  .orders-table .order-item--shipping a {
    color: #2563eb !important;     /* bleu */
    text-decoration: none !important; 
    font-weight: 800;
  }
  .orders-table .order-item--shipping a:hover {
    text-decoration: underline !important; 
  }



  .order-actions .gbtn.gbtn--primary {
    background: linear-gradient(135deg,#3b79ff,#2c5dff) !important;
    color: #fff !important;
    border: 0 !important;
    font-weight: 900 !important;
    border-radius: 12px !important;
    padding: 10px 16px !important;
  }
  .order-actions .gbtn.gbtn--primary:hover {
    filter: brightness(.95);
  }


  .order-details hr {
    border: none;
    border-top: 1px solid #374151; /* gris foncé */
    margin: 0;                     /* plus aucun margin */
    padding: 0;                    /* supprime tout padding éventuel */
    height: 0;                     /* empêche un espace vertical */
    width: 100%;                   /* traverse seulement le bloc */
  }

  .lineBackgroundColor{
    background-color: #eff1f7;
  }




  /* ===== MOBILE (≤560px) — N° + Statut à gauche / Prix + Date à droite ===== */
@media (max-width:560px){
  .orders-head{ display:none; }

  /* grille compacte à 2 colonnes */
  .order-grid{
    display:grid;
    grid-template-columns: 1fr auto;
    grid-template-areas:
      "id     price"
      "status date"
      "btn    btn";
    align-items:center;
    gap:6px;
  }

  /* N° commande (ligne 1, col gauche) */
  .order-id{ grid-area:id; }

  /* Statut juste en dessous du N° */
  .order-status{
    grid-area:status;
    display:block !important;   /* ré-affiche en mobile si caché ailleurs */
    margin-top:2px;
    color:#2563eb;
    font-weight:800;
  }

  /* Prix (ligne 1, col droite) */
  .order-price{
    grid-area:price;
    justify-self:end;
    font-weight:900;
  }

  /* Date (ligne 2, col droite) — le 3e <span> de .order-grid */
  .order-grid > span:nth-of-type(3){
    grid-area:date;
    justify-self:end;
    color:#6b7280;
  }

  /* Bouton Détails en pleine largeur, en dessous */
  .order-detail-btn{
    grid-area:btn;
    justify-self:stretch;
  }
}


.pagination {
  flex-wrap: wrap;
  gap: 4px;
}

.pagination .page-link {
  border-radius: 6px;
  padding: 6px 12px;
  font-size: 14px;
}

.pagination .active .page-link {
  background-color: #0d6efd; /* bleu bootstrap */
  border-color: #0d6efd;
  color: #fff;
}

@media (max-width: 576px) {
  .pagination {
    justify-content: center;
    font-size: 13px;
  }
  .pagination .page-link {
    padding: 5px 10px;
  }
}



/* ===== Séparation des commandes sous forme de cartes ===== */

/* Le conteneur n'est plus une grosse carte */
.orders-table{
  background: transparent;
  border: 0;
  box-shadow: none;
  padding: 0;     /* pas d'espace parasite */
}

/* L'en-tête devient une carte autonome */
.orders-head{
  background: #fff;
  border: 1px solid #e5e7eb;
  border-radius: 12px;
  margin-bottom: 12px;          /* espace sous l'en-tête */
  box-shadow: 0 8px 24px rgba(0,0,0,.06);
}

/* Chaque commande = une carte séparée */
.order-row{
  background: #fff;
  border: 1px solid #e5e7eb;
  border-radius: 12px;
  box-shadow: 0 8px 24px rgba(0,0,0,.06);
  margin: 12px 0;               /* <<< l’espace entre les commandes */
  overflow: hidden;             /* pour arrondis propres entre .order-grid et .order-details */
  border-bottom: 0 !important;  /* on annule l'ancien trait séparateur */
}

/* Le header de la ligne a un fin trait interne, pas en bas de la carte */
.order-grid{
  padding: 12px 14px;
  border-bottom: 1px dashed #f0f0f0;
}

/* Les détails restent nets */
.order-details{
  padding: 12px 14px;
}

/* Un peu plus d'air en mobile aussi */
@media (max-width:560px){
  .order-row{ margin: 10px 0; }
}


/* ===== Pagination moderne (pastilles + slider) ===== */
.orders-pagination-wrap{
  display: grid;
  gap: .6rem;
  align-items: center;
  margin: .5rem 0 1rem;
}

.orders-pagination{
  display: flex;
  flex-wrap: wrap;
  gap: .35rem;
  align-items: center;
}

.pg-btn{
  border: 1px solid #e5e7eb;
  background: #fff;
  padding: .4rem .6rem;
  border-radius: .6rem;
  cursor: pointer;
  font-weight: 800;
  line-height: 1;
  min-width: 36px;
  text-align: center;
  transition: background .15s ease, transform .06s ease;
}
.pg-btn:hover{ background: #f9fafb; }
.pg-btn:active{ transform: translateY(1px); }
.pg-btn.is-active{
  color: #fff;
  background: linear-gradient(135deg,#3b79ff,#2c5dff);
  border-color: transparent;
}
.pg-btn:disabled{
  opacity: .55;
  cursor: not-allowed;
  filter: grayscale(.2);
}

/* Focus visible (a11y) */
.pg-btn:focus-visible,
.pg-slider:focus-visible{
  outline: 2px solid #2a60f5;
  outline-offset: 2px;
  border-radius: 8px;
}

.pg-ellipsis{
  padding: 0 .25rem;
  color: #9ca3af;
  user-select: none;
}

.orders-pagination__meta{
  display: flex;
  align-items: center;
  gap: .8rem;
  justify-content: space-between;
}

.pg-indicator{
  color: #374151;
  font-weight: 700;
}

.pg-slider{
  flex: 1;
  min-width: 160px;
  height: 28px; /* pour une hit-area confortable */
  accent-color: #2a60f5; /* couleur du pouce */
}

/* Réduction de mouvement */
@media (prefers-reduced-motion: reduce){
  .pg-btn{ transition: none; }
}

/* Petits écrans */
@media (max-width: 576px){
  .orders-pagination{ justify-content: center; }
  .orders-pagination__meta{
    gap: .6rem;
    flex-direction: column;
    align-items: stretch;
  }
  .pg-slider{ min-width: 100%; }
}

/* Variante compacte (si besoin sur mobile serré) */
@media (max-width: 380px){
  .pg-btn{ padding: .3rem .5rem; min-width: 32px; }
}

/* Mode sombre léger (si tu bascules un jour) */
@media (prefers-color-scheme: dark){
  .pg-btn{
    background: #111827;
    border-color: #1f2937;
    color: #e5e7eb;
  }
  .pg-btn:hover{ background: #0f172a; }
  .pg-ellipsis{ color: #6b7280; }
  .orders-pagination__meta .pg-indicator{ color: #e5e7eb; }
}


/* ===== Pagination – flèches + numéros sur une seule ligne ===== */
.orders-pagination{
  display:flex;
  align-items:center;
  justify-content:center;
  gap:.35rem;
  flex-wrap:nowrap;           /* une seule ligne */
}

.pg-strip{
  display:flex;
  align-items:center;
  gap:.35rem;
  overflow:hidden;            /* évite tout retour à la ligne */
}

.pg-arrow{
  min-width:36px;
  min-height:36px;
  display:grid;
  place-items:center;
  padding:.4rem;
}

/* un peu plus compact sur mobile */
@media (max-width:560px){
  .pg-btn{ min-width:32px; padding:.35rem .5rem; }
  .pg-arrow{ min-width:32px; min-height:32px; }
}


